import { useState } from "react";
import "./styles.css";

export default function App() {
  const [checkList, setCheckList] = useState([]);
  const [item, setItem] = useState("");

  function addItem() {
    const newList = checkList.concat({
      index: checkList.length + 1,
      name: item
    });
    setCheckList(newList);
    setItem("");
  }

  function clearList() {
    const clearedList = [];
    setCheckList(clearedList);
  }

  function handleRemove(index) {
    const updateList = checkList.filter((item) => item.index !== index);
    setCheckList(updateList);
  }

  return (
    <div className="App">
      <header>
        <h1>My Checklist</h1>
        <div>
          <input
            placeholder="Place your list here"
            type="text"
            value={item}
            onChange={(e) => setItem(e.target.value)}
          />
          <center>
            <button type="button" onClick={addItem}>
              Add Item
            </button>
            <button onClick={clearList}>Clear Checklist</button>
          </center>
          <ul>
            {checkList.map((item) => (
              <li key={item.index} onClick={() => handleRemove(item.index)}>
                {" "}
                {item.name}{" "}
              </li>
            ))}
          </ul>
        </div>
      </header>
    </div>
  );
}
